#portfolio {
  padding-bottom:15rem;
}

#portfolio .row {
    max-width: 1140px;
    margin: 0 auto;
}

.portfolio-heading h2 {
    color: #0092cc;
    text-align: center;
    margin-bottom: 40px;
}

.portfolio-projects {
    display: flex;
    flex-wrap: wrap;
}

/* Overlay img */

.container {
  flex:0 0 50%;
}

.hovereffect {
  width:100%;
  height:100%;
  float:left;
  overflow:hidden;
  position:relative;
  text-align:center;
  cursor:default;
  }
  
  .overlay {
  width:100%;
  height:100%;
  position:absolute;
  overflow:hidden;
  top:0;
  left:0;
  opacity:0;
  background-color:rgba(0,0,0,0.7);
  -webkit-transition:all .4s ease-in-out;
  transition:all .4s ease-in-out
  }
  
  .hovereffect .img-responsive {
  display:block;
  width: 100%;
  position:relative;
  -webkit-transition:all .4s linear;
  transition:all .4s linear;
  }
  
  .hovereffect h3 {
  text-transform:uppercase;
  color:#fff;
  text-align:center;
  position:relative;
  font-size:1.8rem;
  background:rgba(0,0,0,0.6);
  -webkit-transform:translatey(-100px);
  -ms-transform:translatey(-100px);
  transform:translatey(-100px);
  -webkit-transition:all .2s ease-in-out;
  transition:all .2s ease-in-out;
  padding:10px;
  }
  
  .hovereffect .info {
  text-decoration:none;
  display:block;
  font-size: 1.5rem;
  text-transform:uppercase;
  letter-spacing: .08rem;
  color:#fff;
  background-color:transparent;
  opacity:0;
  filter:alpha(opacity=0);
  -webkit-transition:all .2s ease-in-out;
  transition:all .2s ease-in-out;
  width: 100%;
  height: 100%;
  }
    
  .hovereffect:hover .img-responsive {
  -ms-transform:scale(1.1);
  -webkit-transform:scale(1.1);
  transform:scale(1.1);
  }
  
  .hovereffect:hover .overlay {
  opacity:1;
  filter:alpha(opacity=100);
  }
  
  .hovereffect:hover h3,.hovereffect:hover .info {
  opacity:1;
  filter:alpha(opacity=100);
  -ms-transform:translatey(0);
  -webkit-transform:translatey(0);
  transform:translatey(0);
  }
  
  .hovereffect:hover .info {
  -webkit-transition-delay:.2s;
  transition-delay:.2s;
  }

  /* modal window */

  .pop-content {
    border: none !important;
    padding: 0 !important;
    max-width: 550px;
    background: #FFFFFF;

  }

  .modal .modal-img {
    width: 100%;
    height: auto;
  }

  .description-box {
    padding: 1.8rem 3.6rem 3rem;
  }

  .description-box h4 {
    text-align: left;
    font-family: "poppins-bold", sans-serif;
    font-size: 1.5rem;
    line-height: 2.4rem;
    margin-bottom: .6rem;
  }

  .description-box p {
    text-align: justify;
    font-family: "poppins-regular", sans-serif;
    font-size: 1.4rem;
    line-height: 2.4rem;
    margin-bottom: 12px;
}

.actions {
  display: flex;
  flex-direction: row;
  background: #000000;
}

.pop-button {
    font-family: "poppins-bold", sans-serif;
    text-decoration: none;
    font-size: 1.2rem;
    line-height: 6rem;
    color: #FFFFFF;
    text-transform: uppercase;
    letter-spacing: 3px;
    cursor: pointer;
    display: block;
    width: 50%;
    margin: 0;
    padding: 0;
}

.pop-button:hover {
  background-color: #0092cc;

}