}

p {
    font-family: "lora-regular", serif;
    font-size: 1.7rem;
    line-height: 3rem;
    color: #6e6e6e;
}
.button {
    display: inline-block;
    text-decoration: none;
    padding: 1.8rem 3.5rem 1.8rem 3.5rem;
    font-size: 1.3rem;
    text-transform: uppercase;
    letter-spacing: .25rem;
}

.transition-smooth {
