    }
}

@media only screen and (max-width: 768px){

    html {
        font-size: 9px;
    }

    .intro-content h1 {
        font-size: 8.8vw;
        letter-spacing: 0px;
    }

    .intro-content .intro-position {
        font-size: 1.3rem;
    }

    .about-content {
        flex-direction: column;
    }

    .about-content .col-five {
        padding: 0 20px;
    }

    .about-content h3 {
        text-align: center;
        margin-top:18px;
    }

    .button-section .button {
        width: 70%;
        margin-bottom: 3rem;
    }

    .pop-content {
        width: 100% !important; 
      }

}

@media only screen and (max-width: 600px){
.intro-content .intro-position {
    font-size: 1.2rem;
    }

    .intro-position span:first-child::before {
        content: "";
        padding: 0;

       }

       .intro-position span::after {
        content: "";
        padding: 0;

       }

       .intro-position span {
           margin: 5px;
       }

       #portfolio .portfolio-projects {
           flex-direction: column;
       }

       .footer-social-place {
        flex-direction: column-reverse;
        align-items: center;
    }

    #footer .col-half:last-child {
        margin-bottom: 20px;
    }

    .footer-social {
        font-size: 2.8rem;
    }

    .footer-social li {
        margin:0 14px;

    }
    
    .copyright {
        font-size: 1.8rem;
    }
           
}

@media only screen and (max-width: 470px){
    .intro-position {
        text-align: center;
        
    }

    
    [class*="col-"] {
        padding: 0 5px;
    }

    .intro-social li {
